<template>
<div>
	<!--  Show Gmail One Click widget only if Pro is NOT active -->
	<post-smtp-widget v-if="!postSmtpPro" class="gmail-wrapper">
		<div class="img-wrapper">
			<a class="gmail-click" target="_blank" href="https://postmansmtp.com/pricing/?utm_source=plugin&utm_medium=dashboard&utm_campaign=gmail_one_click">
		<post-smtp-img class="gmail-one-click" :src="plugin_dir_url + 'assets/images/gmail_one_click.gif'" />
			</a>
		</div>
	</post-smtp-widget>

	<!--  Show Ads only if Pro is active and configured -->
	<post-smtp-ads
		v-if="postSmtpPro && postSmtpConfigured"
		class="mb-24"
		:post-smtp-pro="postSmtpPro"
		:is-mobile-app-configured="isMobileAppConfigured"
		:is-main-wp-configured="isMainWpConfigured"
		:is-domain-spam-score-configured="isDomainSpamScoreConfigured"
	/>

	<!--  Always show Troubleshooting widget -->
	<post-smtp-widget class="troubleshooting">
		<div class="troubleshooting-header">
			{{ i18n.titleTroubleshooting }}
		</div>
		<div class="troubleshooting-content">
			<ul>
				   <li v-for="(troubleshoot, idx) in troubleshooting" class="troubleshoot">
					   <div v-if="troubleshoot.show">
						   <span v-if="!troubleshoot.image" style="margin-right: 15px; color: #3c5daa;" :class="troubleshoot.icon"></span>
						   <img v-if="troubleshoot.image && troubleshoot.title" style="margin-bottom: -5px; margin-right: 16px;" :src="plugin_dir_url + 'assets/images/' + troubleshoot.icon" />
						   <!-- Special handling for Mailer Logs link (index 6) -->
						   <template v-if="troubleshoot.icon === 'mailer.svg'">
							   <template v-if="postSmtpPro">
								   <a :href="troubleshoot.url">{{ troubleshoot.title }}</a>
							   </template>
							   <template v-else>
								   <a href="#" @click.prevent="showMailerLogsPopup = true" style="color: #8B8F9A; text-decoration: underline; cursor: pointer;">
									   {{ troubleshoot.title }} <span style="background: #f5c542; color: #fff; border-radius: 3px; font-size: 10px; padding: 2px 4px; margin-left: 4px;">PRO</span>
								   </a>
							   </template>
						   </template>
						   <template v-else>
							   <a :target="troubleshoot.target" :href="troubleshoot.url">
								   {{ troubleshoot.title }}
								   <span v-if="troubleshoot.target" style="color: #8B8F9A; font-size: 15px; margin-left: -3px;" class="dashicons dashicons-external"></span>
							   </a>
						   </template>
					   </div>
				   </li>

			<!-- Mailer Logs Popup -->
			<div v-if="showMailerLogsPopup" class="mailer-logs-popup-overlay">
				<div class="mailer-logs-popup">
					<span class="mailer-logs-popup-close" @click="showMailerLogsPopup = false">&#10005;</span>
					<div class="mailer-logs-popup-header">Mailers Logs</div>
					<div class="mailer-logs-popup-body">
						The troubleshooting tool Mailer Logs helps you track emails sent through your chosen mailer.
					</div>
					<div class="mailer-logs-popup-footer">
						<button class="mailer-logs-upgrade-btn" @click="goToUpgrade">
							Upgrade to a Pro
							<span class="arrow-right"></span>
						</button>
					</div>
				</div>
			</div>
	</ul>
		</div>
	</post-smtp-widget>

	<!--  Always show Customer Support widget -->
	<post-smtp-widget class="customer-support">
		<div>
			<post-smtp-img :src="plugin_dir_url + 'assets/images/customer-support.png'" />
		</div>
		<div class="customer-support-content">
			<p>
				{{ i18n.appointment.description }}
			</p>
		</div>
		<div>
			<post-smtp-button
				href="https://postmansmtp.com/configuration-request/?utm_source=plugin&utm_medium=dashboard_side_banner&utm_campaign=plugin"
				type="secondary"
			>
				{{ i18n.appointment.button }}
			</post-smtp-button>
		</div>
	</post-smtp-widget>

</div>


</template>

<script>
export default {
	name: 'PostSmtpSidebar',

	props: {
		postSmtpPro: {
			type: Boolean,
			default: false,
		},
		postSmtpConfigured: {
			type: Boolean,
			default: false,
		},

		isMobileAppConfigured: {
			type: Boolean,
			default: false,
		},

		isMainWpConfigured: {
			type: Boolean,
			default: false,
		},

		isDomainSpamScoreConfigured: {
			type: Boolean,
			default: false,
		},
	},

	data: ( vm ) => ( {
		plugin_dir_url: vm.$parent.plugin_dir_url,
		admin_url: vm.$parent.admin_url,
    	i18n: postSmtpNewDashboard.i18n.sidebar,
		troubleshooting: [
			{
				title: postSmtpNewDashboard.i18n.sidebar.troubleshooting[0],
				icon: 'email.svg',
				url: vm.$parent.admin_url + '?page=postman/email_test',
				image: true,
				target: false,
				show: true,
			},
			{
				title: postSmtpNewDashboard.i18n.sidebar.troubleshooting[1],
				url: 'https://postmansmtp.com/domain-health-checker/',
				icon: 'dashicons dashicons-performance',
				image: false,
				target: '_blank',
				show: true,
			},
			{
				title: postSmtpNewDashboard.i18n.sidebar.troubleshooting[2],
				icon: 'import-export.svg',
				url: vm.$parent.admin_url + '?page=postman/manage-options',
				image: true,
				target: false,
				show: true,
			},
			{
				title: postSmtpNewDashboard.i18n.sidebar.troubleshooting[3],
				icon: 'connectivity.svg',
				url: vm.$parent.admin_url + '?page=postman/port_test',
				image: true,
				target: false,
				show: true,
			},
			{
				title: postSmtpNewDashboard.i18n.sidebar.troubleshooting[4],
				icon: 'diagnostic.svg',
				url: vm.$parent.admin_url + '?page=postman/diagnostics',
				image: true,
				target: false,
				show: true,
			},
			{
				title: postSmtpNewDashboard.i18n.sidebar.troubleshooting[5],
				icon: 'reset.svg',
				url: vm.$parent.admin_url + '?page=postman/manage-options',
				image: true,
				target: false,
				show: true,
			},
			{
				title: postSmtpNewDashboard.i18n.sidebar.troubleshooting[6],
				icon: 'mailer.svg',
				url: vm.$parent.admin_url + '?page=postman_mailer_logs',
				image: true,
				target: false,
				show: true,
			},
		]
	} ),
	data(vm) {
		const orig = typeof vm === 'object' && vm.$parent ? {
			plugin_dir_url: vm.$parent.plugin_dir_url,
			admin_url: vm.$parent.admin_url,
		} : {};
		return {
			...orig,
			i18n: postSmtpNewDashboard.i18n.sidebar,
			troubleshooting: [
				{
					title: postSmtpNewDashboard.i18n.sidebar.troubleshooting[0],
					icon: 'email.svg',
					url: orig.admin_url + '?page=postman/email_test',
					image: true,
					target: false,
					show: true,
				},
				{
					title: postSmtpNewDashboard.i18n.sidebar.troubleshooting[1],
					url: 'https://postmansmtp.com/domain-health-checker/',
					icon: 'dashicons dashicons-performance',
					image: false,
					target: '_blank',
					show: true,
				},
				{
					title: postSmtpNewDashboard.i18n.sidebar.troubleshooting[2],
					icon: 'import-export.svg',
					url: orig.admin_url + '?page=postman/manage-options',
					image: true,
					target: false,
					show: true,
				},
				{
					title: postSmtpNewDashboard.i18n.sidebar.troubleshooting[3],
					icon: 'connectivity.svg',
					url: orig.admin_url + '?page=postman/port_test',
					image: true,
					target: false,
					show: true,
				},
				{
					title: postSmtpNewDashboard.i18n.sidebar.troubleshooting[4],
					icon: 'diagnostic.svg',
					url: orig.admin_url + '?page=postman/diagnostics',
					image: true,
					target: false,
					show: true,
				},
				{
					title: postSmtpNewDashboard.i18n.sidebar.troubleshooting[5],
					icon: 'reset.svg',
					url: orig.admin_url + '?page=postman/manage-options',
					image: true,
					target: false,
					show: true,
				},
				{
					title: postSmtpNewDashboard.i18n.sidebar.troubleshooting[6],
					icon: 'mailer.svg',
					url: orig.admin_url + '?page=postman_mailer_logs',
					image: true,
					target: false,
					show: true,
				},
			],
			showMailerLogsPopup: false
		};
	},
	methods: {
		goToUpgrade() {
			window.open('https://postmansmtp.com/pricing/?utm_source=plugin&utm_medium=dashboard&utm_campaign=mailer_logs_upgrade', '_blank');
		}
	}
}
</script>

