	position: absolute;
	top: 10px;
	right: 14px;
	font-size: 20px;
	color: #214A72;
	cursor: pointer;
	font-weight: 700;
	z-index: 2;
}
.mailer-logs-popup {
	position: relative;
}

.mailer-logs-popup-overlay {
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	background: rgba(0,0,0,0.4);
	display: flex;
	align-items: center;
	justify-content: center;
	z-index: 9999;
}
.mailer-logs-popup {
	width: 327px;
	height: 163.09px;
	border-radius: 8.2px;
	border-width: 0.25px;
	background: linear-gradient(350deg, #FFFFFF, #8ED2F5), linear-gradient(122deg, #DEEDF7 56.58%, #8ED2F5 90.53%);
	box-shadow: 0 2px 16px rgba(0,0,0,0.08);
	padding: 24px 18px 18px 18px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	text-align: center;
}
.mailer-logs-popup-header {
	font-family: 'Bricolage Grotesque', sans-serif;
	font-weight: 600;
	font-style: SemiBold;
	font-size: 16.39px;
	line-height: 100%;
	letter-spacing: -0.27px;
	text-align: center;
	color: #214A72;
	margin-bottom: 12px;
}
.mailer-logs-popup-body {
	font-family: 'Bricolage Grotesque', sans-serif;
	font-weight: 400;
	font-style: normal;
	font-size: 11.47px;
	line-height: 100%;
	letter-spacing: -0.27px;
	text-align: center;
	color: #000;
	margin-bottom: 24px;
	width: 250px;
}
.mailer-logs-popup-footer {
	display: flex;
	gap: 12px;
	justify-content: center;
}
.mailer-logs-upgrade-btn {
	background: linear-gradient(270deg, #F9AA39 0%, #FFDAA5 100%),
		linear-gradient(124.11deg, #FFF0C5 2.93%, #FB9E1F 88.64%);
	color: #231F20;
	border: none;
	border-radius: 4px;
	padding: 8px 18px;
	font-family: 'Bricolage Grotesque', sans-serif;
	font-weight: 600;
	font-style: normal;
	font-size: 11.47px;
	line-height: 16.39px;
	letter-spacing: 0px;
	cursor: pointer;
	display: flex;
	align-items: center;
	gap: 6px;
	box-shadow: 0 1px 4px rgba(249,170,57,0.08);
	border-radius:20px
}
.mailer-logs-upgrade-btn .arrow-right {
	font-size: 18px;
	margin-left: 4px;
	display: inline-block;
}
.mailer-logs-close-btn {
	background: #e0e0e0;
	color: #333;
	border: none;
	border-radius: 4px;
	padding: 8px 18px;
	font-size: 15px;
	font-weight: 600;
	cursor: pointer;
}

.customer-support {
	padding: 0 !important;
	background: linear-gradient(180deg, #40518E 0%, #231F20 100%);
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	height: 268px;
}

.customer-support-content {
	width: 213px;
}

.customer-support-content p {
	font-family: Poppins;
	font-size: 14px;
	font-weight: 500;
	line-height: 20px;
	text-align: center;
	color: #B8BDCA;
}

.troubleshooting {
	padding: 0 !important;
}

.mb-24 {
	margin-bottom: 24px;
}

.troubleshooting-header {
	border-radius: 4px 4px 0 0;
	background: #EFF4FD;
	padding: 16px 24px;

	font-family: Poppins;
	font-size: 16px;
	font-weight: 600;
	line-height: 20px;
	color: #231F20;
}


.troubleshooting-content {
	padding: 24px;
}

.troubleshoot.email {
	list-style: url("../images/email.svg") inside;
}

.troubleshoot.import-export {
	list-style: url("../images/import-export.svg") inside;
}

.troubleshoot.connectivity {
	list-style: url("../images/connectivity.svg") inside;
}

.troubleshoot.diagnostic {
	list-style: url("../images/diagnostic.svg") inside;
}

.troubleshoot.reset {
	list-style: url("../images/reset.svg") inside;
}

.troubleshoot a {
	font-family: Poppins;
	font-size: 14px;
	font-weight: 400;
	line-height: 20px;
	color: #8B8F9A;
	text-decoration: underline;
}
